<template>
    <div class="child" @click="imgtab()">
        <span v-show="bol"><slot name="norimg"></slot><br/></span>
        <span v-show="!bol"><slot name="actimg"></slot><br/></span>
        <span :class="{act:!bol}">{{txt}}</span>
    </div>
</template>

<script>

export default {
  props:['txt','mark','sel'],
  methods:{
      imgtab:function(){
         this.$emit('change',this.mark);
         this.$router.push('/'+this.mark);
      }
  },
  computed:{
      bol:function(){
          if(this.mark==this.sel){
              return false;
          }
          return true;
      }
  }

}
</script>

<style>
input,button{border: none}
li{list-style: none}
.child{width: 20%;text-align: center;float: left;}
.child span{height: 18px;line-height: 18px;font-family: '微软雅黑';font-size: 14px;color:#767573;}
.child .act{color: #3bb644;}
.child img{width: 35px;}
</style>



