<template>
  <div class="amain clearfix">
      <div class="am-top">
            <atext :asrc="require('../../assets//image/ic_status_anonymous_header.png')" atxt1="发现有趣的人" atxt2="关注他们，发现更大的世界"></atext>
            <div class="aitem" v-if="val.show" v-for="(val,i) in arr">
                <div class="left">
                  <img :src="val.asrc"/>
                </div>
                <div class="right">
                    <h3>{{val.ah3}}</h3>
                    <span class="sp1">{{val.asp1}}人关注</span>
                    <p>{{val.ap}}</p>
                    <div class="bot">
                        <button class="but1" type="button" @click="val.numa++">{{val.numa}}</button>
                        <button class="but2" type="button" @click="val.numb++">{{val.numb}}</button>
                    </div>
                    <button type="button" :class="['but3',{act:val.bol}]" @click="val.bol=!val.bol;alt(i)">{{val.text}}</button>
                    <i @click="close(i)">X</i>
                </div>
            </div>
      </div>
      <div class="am-bot">
            <span>换一批</span>
            <button type="button" :class="{act:state}">选好了，进入广播</button>
      </div>
  </div>
</template>

<script>

import atext from './atext'


export default {

  data:function(){
    return{
      arr:[
        {'asrc':require('../../assets/image/2.pic_03.jpg'),'ah3':"陈苍苍",'asp1':532288,'ap':"我们没小孩的也很不容易啊.我们没小孩的也很不容易啊我们没小孩的也很不容易啊",'numa':50,'numb':24,'show':true,'text':'关注','bol':false},
        {'asrc':require('../../assets/image/2.pic_07.jpg'),'ah3':"张晓晓",'asp1':4628,'ap':"菜市场颜色真好看",'numa':30,'numb':44,'show':true,'text':'关注','bol':false}
      ],
      state:false
    }
  },
  methods:{
    close:function (i) {
      this.arr[i].show=false;
    },
    alt:function(i){
      this.arr[i].text='已关注';
      var count=0;
      for(var k=0;k<this.arr.length;k++){
        if(this.arr[k].bol==true){
          this.state=true;
        }
        if(this.arr[k].bol==false){
          count++;
        }
      }
      if(count==this.arr.length){
        this.state=false;
      }
    }
  },
  components:{atext}



}
</script>

<style>
*{margin: 0;padding: 0;}
.clearfix::after{content: '.';display: block;height: 0;clear: both;visibility: hidden;}
.amain{width: 100%;background: #f7f7f7;padding-top: 61px;padding-bottom: 121px;}
.am-top{width: 100%;}
.am-bot{width:100%;height: 60px;background: white;box-shadow: 0 -5px 5px rgba(0,0,0,0.1);position: fixed;bottom: 61px;left: 0;}
.amain .am-bot span{float: left;margin-left: 30px;background: url("../../assets/image/ic_reshare_gray.png") no-repeat left;padding-left: 30px;background-size: 30%;height: 60px;line-height: 60px;cursor: pointer;}
.amain .am-bot button{float: right;height: 40px;width: 150px;background: #dfdfdf;margin-top: 10px;color: white;line-height: 40px;text-align: center;font-size: 16px;border-radius:5px;margin-right: 10px;cursor: pointer; }
.amain .am-bot .act{background: #42c055;}
i{font-style: normal;}
button{border: none;outline: none;cursor: pointer;}
.amain .aitem{width: 330px;height: 250px;background: white;margin:10px auto;border-radius: 10px;box-shadow: 0 0 15px rgba(0,0,0,0.5);}
.amain .aitem .left{float: left;width: 100px;height: 100%;position: relative;}
.amain .aitem .left img{position: absolute;left: 20px;top:20px;width: 60px;}
.amain .aitem .right{text-align: left;position: relative;float: left;height: 100%;width: 230px;}
.amain .aitem .right h3{font-size: 16px;height: 25px;line-height: 25px;font-family: '微软雅黑';color: #5f5f5f;padding-top: 20px;}
.amain .aitem .right .sp1{font-size: 14px;height: 25px;line-height: 25px;font-family: '微软雅黑';color: #adadad;}
.amain .aitem .right p{font-size: 16px;line-height: 20px;font-family: '微软雅黑';color: #323232;padding-right: 30px;}
.amain .aitem .right i{position: absolute;right: 5px;top:5px;font-size: 16px;color: #dcdcdc;cursor: pointer;}
.amain .aitem .right .but3{position: absolute;top:20px;right: 30px;height: 30px;width: 60px;text-align: center;background: #41c154;border-radius:8px;color: white;line-height: 30px;font-family:'微软雅黑';font-size: 14px;}
.amain .aitem .right .bot{position: absolute;left: 5px;bottom: 10px;}
.amain .aitem .right .act{background: #dfdfdf;}
.amain .aitem .right .bot button{text-align: center;height: 30px;line-height: 30px;color: #e4e4e4;font-family: Arial;font-size: 14px;width: 60px;float: left;}
.amain .aitem .right .bot .but1{background: white url('../../assets/image/ic_like_full.png') no-repeat left center;background-size:25%;}
.amain .aitem .right .bot .but2{background: white url('../../assets/image/ic_group_comment_0.png') no-repeat left center;background-size:35%;}
</style>



