<template>
  <div class="film">
      <div class="top">
          <span>更多 ></span>
          <h3>影院热映</h3>
          <div class="ulbox">
              <ul :style="{width:w+'px'}">
                <li v-for="(val,i) in oli">
                  <img :src="val.src" class="pic"/>
                  <h4>{{val.h4}}</h4>
                  <div><img :src="xinpic[0]" v-for="key in parseInt(Math.ceil(val.i)/2)"/><img :src="xinpic[1]" v-for="key in parseInt(Math.ceil(val.i)%2)"/><img :src="xinpic[2]" v-for="key in (5-parseInt(Math.ceil(val.i)/2)-parseInt(Math.ceil(val.i)%2))"/><i>{{val.i}}</i></div>
                </li>
              </ul>
          </div>
      </div>
      <div class="mid">
        <img src="../../assets/image/film_02.jpg"/>
        <p>"康熙"合体，小S放飞自我，白眼怒干恶评</p>
      </div>

    <div class="top">
      <span>更多 ></span>
      <h3>影院热映</h3>
      <div class="ulbox">
        <ul :style="{width:w+'px'}">
          <li v-for="(val,i) in oli">
            <img :src="val.src" class="pic"/>
            <h4>{{val.h4}}</h4>
            <div><img :src="xinpic[0]" v-for="key in parseInt(Math.ceil(val.i)/2)"/><img :src="xinpic[1]" v-for="key in parseInt(Math.ceil(val.i)%2)"/><img :src="xinpic[2]" v-for="key in (5-parseInt(Math.ceil(val.i)/2)-parseInt(Math.ceil(val.i)%2))"/><i>{{val.i}}</i></div>
          </li>
        </ul>
      </div>
    </div>
    <div class="mid">
      <img src="../../assets/image/film_02.jpg"/>
      <p>"康熙"合体，小S放飞自我，白眼怒干恶评</p>
    </div>
  </div>
</template>

<script>

export default {
    data:function () {
      return{
          oli:[
            {
              'src':require('../../assets/image/film_03.jpg'),
              'h4':'加勒比海盗5：...',
              'i':7.5
            },
            {
              'src':require('../../assets/image/film_05.jpg'),
              'h4':'荡寇风云',
              'i':6.2
            },
            {
              'src':require('../../assets/image/film_07.jpg'),
              'h4':'"吃吃"的爱',
              'i':6.4
            },
            {
              'src':require('../../assets/image/film_03.jpg'),
              'h4':'加勒比海盗5：...',
              'i':7.5
            }
          ],
        xinpic:[
            require('../../assets/image/rating_star_xsmall_on.png'),
            require('../../assets/image/rating_star_xsmall_half.png'),
            require('../../assets/image/rating_star_xsmall_off.png')
        ]
      }
    },
    computed:{
        w:function(){
          return this.oli.length*110;
        }
    }

}
</script>

<style>
*{padding: 0;margin: 0;}
input,button,img{border: none}
li{list-style: none}
i{font-style: normal;}
.film{background: #f7f7f7;padding-top: 10px;}
.film .top{background: white;margin-bottom: 10px;}
.film .top span{float: right;height: 70px;line-height: 70px;font-family: 'consolas';font-size: 16px;color: #5cab72;cursor: pointer;margin-right: 20px;}
.film .top h3{text-align: left;height: 70px;line-height: 70px;font-family: '微软雅黑';font-size: 20px;color: #505050;margin-left:20px;}
.film .top .ulbox{width: 100%;height: 200px;overflow: auto;}
.film .top ul{height: 200px;margin-left:20px;text-align: left;float: left;}
.film .top ul li{height: 200px;float: left;margin-right: 10px;width: 100px;overflow: hidden;}
.film .top ul li .pic{width:100px;display: block;}
.film .top ul li h4{height: 24px;line-height: 24px;font-size: 14px;overflow: hidden;font-family: '微软雅黑';color: #323232;}
.film .top ul li div{height: 20px;overflow: hidden;width: 100px;}
.film .top ul li div i{height: 20px;line-height: 20px;font-size: 14px;float: left;font-family: '微软雅黑';color: #c0c0c0;}
.film .top ul li div img{float: left;width: 15px;}
.film .mid{width: 100%;background: white;margin-bottom: 10px;overflow: hidden;}
.film .mid img{height: 120px;}
.film .mid p{height: 40px;line-height: 40px;font-family: '微软雅黑';font-size: 18px;font-weight: bold;color: #3d3d3d;}
</style>



