<template>
  <div class="swiper-container" :class="swipeid">
      <div class="swiper-wrapper">
          <slot name="swiper-icon"></slot>
      </div>
      <div :class='{"swiper-pagination":pagination}' :style='{"text-align":paginationDirection}'></div>
  </div>
</template>

<script>
  import '../../assets/js/swiper.js'
export default {
    props:{
      swipeid:{         //轮播样式
        type:String,
        default:''
      },
      effect:{          //轮播效果
        type:String,
        default:'slide'
      },
      loop:{            //是否轮播
        type:Boolean,
        default:true
      },
      direction:{       //轮播方向
        type:String,
        default:'horizontal'
      },
      autoplay:{          //轮播时间间隔
        type:String,
        default:'2000'
      },
      paginationType:{      //分页类型
        type:String,
        default:'bullets'
      },
      pagination:{        //是否有分页
        type:Boolean,
        default:true
      },
      paginationDirection:{       //分页方向
        type:String,
        default:'center'
      }
    },

  mounted:function(){
    var that=this;
    new Swiper('.'+that.swipeid,{
      loop:that.loop,
      pagination:'.swiper-pagination',
      paginationType:that.paginationType,
      autoplay:that.autoplay,
      direction:that.direction,
      effect:that.effect,
      autoplayDisableOnInteraction:false                   //手动干预就暂停轮播

    })
  }


}
</script>


<style>
@import "../../assets/css/swiper.min.css";
*{padding:0;margin:0;}

</style>



