<template>
        <div class="footer">
            <item txt="首页" :sel="select" mark="home" @change="getval">
                <img src="../assets/image/ic_tab_home_normal.png" slot="norimg">
                <img src="../assets/image/ic_tab_home_active.png" slot="actimg">
            </item>
            <item txt="书影音" :sel="select" mark="book" @change="getval">
                <img src="../assets/image/ic_tab_subject_normal.png" slot="norimg">
                <img src="../assets/image/ic_tab_subject_active.png" slot="actimg">
            </item>
            <item txt="广播" :sel="select" mark="audio" @change="getval">
                <img src="../assets/image/ic_tab_status_normal.png" slot="norimg">
                <img src="../assets/image/ic_tab_status_active.png" slot="actimg">
            </item>
            <item txt="小组" :sel="select" mark="group" @change="getval">
                <img src="../assets/image/ic_tab_group_normal.png" slot="norimg">
                <img src="../assets/image/ic_tab_group_active.png" slot="actimg">
            </item>
            <item txt="我" :sel="select" mark="mine" @change="getval">
                <img src="../assets/image/ic_tab_profile_normal.png" slot="norimg">
                <img src="../assets/image/ic_tab_profile_active.png" slot="actimg">
            </item>
        </div>
    </template>

<script>

import item from './foot-child'

export default {
    data:function(){
            return{
                select:'home'
            }
          },
    methods:{
      getval:function(val){
          this.select=val;
      }
    },
   components:{item}

}
</script>
<style>
input,button{border: none}
li{list-style: none}
.footer{background: #f4f3ee;height: 60px;width: 100%;position: fixed;bottom: 0;left: 0;border-top:1px solid #bbbbbb;z-index: 50;}
</style>




